h6,
.heading-style-h6, h5,
.heading-style-h5, h4,
.heading-style-h4, h3,
.heading-style-h3, h2,
.heading-style-h2, h1,
.heading-style-h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "FT Sterling", Verdana, sans-serif;
  font-weight: 600;
}

.text-size-tiny, .text-size-small, .text-size-regular, .text-size-medium, .text-size-large, .text-size-xlarge, body {
  margin: 0;
  font-family: "Mona Sans", Verdana, sans-serif;
  font-weight: 400;
}

.hero__text--cta {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  border-radius: calc(infinity * 1px);
  font-family: "SF Pro";
}

.stories__text, .testimonials__text, .collection__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 18px;
  margin-bottom: 64px;
}
.stories__text h2, .testimonials__text h2, .collection__text h2 {
  color: #465039;
  font-size: 64px;
  text-transform: uppercase;
  font-family: "Ivy Mode";
  font-weight: 300;
}
.stories__text > div, .testimonials__text > div, .collection__text > div {
  width: 100%;
  padding-top: 36px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #ADB4A4;
}
.stories__text > div p, .testimonials__text > div p, .collection__text > div p {
  font-size: 18px;
  font-family: "SF Pro";
  color: #5E6653;
  width: 950px;
}
.stories__text > div > div, .testimonials__text > div > div, .collection__text > div > div {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
@media (max-width: 991px) {
  .stories__text > div > div, .testimonials__text > div > div, .collection__text > div > div {
    display: none;
  }
}
.stories__text > div > div i, .testimonials__text > div > div i, .collection__text > div > div i {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  color: #5E6653;
  line-height: 52px;
  text-align: center;
  border: 1px solid #5E6653;
}

body * {
  color: inherit;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

section,
.section {
  position: relative;
  overflow: hidden;
}

a {
  color: inherit;
  text-decoration: inherit;
  font-size: inherit;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.container-small,
.container-medium,
.container-large {
  margin-right: auto !important;
  margin-left: auto !important;
}

body {
  font-size: 1.125rem;
  line-height: 1.4em;
  letter-spacing: 0em;
}

h1,
.heading-style-h1 {
  font-size: 4.75rem;
  line-height: 1.2em;
  letter-spacing: 0.01em;
}
@media only screen and (max-width: 767px) {
  h1,
  .heading-style-h1 {
    font-size: 3.75rem;
  }
}

