.hero {
  height: 100vh;
  width: 100%;
  background-image: url("../../../assets/media/images/daisies 1.png");
  background-color: #D9DED2;
  background-size: 770px;
  background-position: bottom;
  background-repeat: no-repeat;
  padding-top: 48px;
  position: relative;
}
@media (max-width: 991px) {
  .hero {
    padding: 50px 0;
  }
}
.hero__text {
  padding: 0 80px;
  width: 75%;
}
@media (max-width: 991px) {
  .hero__text {
    width: 100%;
    padding: 0 40px;
  }
}
.hero__text--title {
  color: #465039;
  margin-bottom: 12px;
  font-size: 76px;
  text-transform: uppercase;
  font-family: "Ivy Mode";
  font-weight: 300;
}
.hero__text--paragraph {
  width: 513px;
  font-family: "SF Pro";
  color: #5E6653;
  font-size: 18px;
}
.hero__text--cta {
  width: 268px;
  height: 57px;
  background-color: #5E6653;
  color: white;
  margin-top: 24px;
  border: none;
}
.hero__image {
  background-color: white;
  position: absolute;
  width: 264px;
  padding: 12px 12px 16px 12px;
  top: 15%;
  right: 5%;
  transform: rotate(1deg);
}
@media (max-width: 991px) {
  .hero__image {
    top: 45%;
    left: 55%;
  }
}
.hero__image--img {
  transform: rotate(-1deg);
  width: 100%;
  height: 280px;
}
.hero__image p {
  font-size: 18px;
}
.hero__image span {
  font-size: 14px;
}
.hero__image--icon {
  width: 96px;
  height: 96px;
  border-radius: 50%;
  position: absolute;
  top: -2.5rem;
  right: -2rem;
}

.quality {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #E2E7DB;
  width: 100%;
  height: 144px;
  padding: 40px 80px;
  box-shadow: 0 -100px 10px rgba(0, 0, 0, 0.2);
}
@media (max-width: 991px) {
  .quality {
    overflow-x: scroll;
    scrollbar-width: none;
    justify-content: start;
    gap: 36px;
  }
}
.quality__item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
@media (max-width: 991px) {
  .quality__item {
    text-wrap: nowrap;
    width: 100%;
  }
}
.quality__item img {
  width: 64px;
  height: 64px;
  padding: 10px;
  background-color: #5E6653;
  border-radius: 50%;
}
.quality__item--text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
}
.quality__item--text h6 {
  font-size: 24px;
  font-weight: 300;
  font-family: "Ivy Mode";
  color: #5E6653;
  text-transform: capitalize;
}
.quality__item--text p {
  font-size: 18px;
  font-family: "SF Pro";
  color: #5E6653;
}

.introduction {
  background-color: #F3F6F0;
  padding: 76px 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 64px;
}
@media (max-width: 991px) {
  .introduction {
    flex-direction: column;
    padding: 76px 40px;
    gap: 48px;
  }
}
.introduction__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  width: 732px;
  gap: 36px;
}
@media (max-width: 991px) {
  .introduction__text {
    width: 100%;
  }
}
.introduction__text--title {
  font-size: 64px;
  font-weight: 300;
  font-family: "Ivy Mode";
  text-transform: uppercase;
  color: #465039;
}
.introduction__text--paragraph {
  font-family: "SF Pro";
  color: #5E6653;
  font-size: 18px;
}
.introduction__text--cta {
  width: 209px;
  height: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background-color: #5E6653;
  color: #F3F6F0;
  font-size: 16px;
  font-weight: 400;
  font-family: "SF Pro";
  border: none;
  text-transform: uppercase;
  border-radius: calc(infinity * 1px);
}
.introduction__gallery {
  display: flex;
  justify-content: center;
  width: 485.3px;
  gap: 10px;
}
.introduction__gallery--left, .introduction__gallery--right {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.introduction__gallery--left img, .introduction__gallery--right img {
  width: 100%;
  border: 6px solid white;
}
.introduction__gallery--left {
  align-items: end;
}
.introduction__gallery--left img:nth-child(2) {
  width: 164px;
  height: 164px;
}
.introduction__gallery--right {
  align-items: start;
}
.introduction__gallery--right img:nth-child(1) {
  width: 154px;
  height: 154px;
}

.collection {
  width: 100%;
  padding: 76px 80px;
  background-color: #D9DED2;
}
@media (max-width: 991px) {
  .collection {
    padding: 76px 40px;
  }
}
.collection__list {
  display: flex;
  align-items: center;
  justify-content: start;
  width: fit-content;
  gap: 36px;
}
@media (max-width: 991px) {
  .collection__list {
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
  }
}
.collection__list--item:nth-child(even) {
  transform: rotate(1deg);
}
.collection__list--item:nth-child(odd) {
  transform: rotate(-1deg);
}
.collection__list--item {
  width: 293px;
  background-color: white;
  padding: 16px 16px 20px 16px;
}
.collection__list--item--image {
  width: 100%;
  margin-bottom: 20px;
}
.collection__list--item--text {
  display: flex;
